* {
    padding: 0;
    margin: 0 auto;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
body {
    height: 90vh;
}

.display-4 {
    font-family: 'Lato', sans-serif;
    font-weight : bold;
    font-size: 58px;
    text-align: center;
    display: block;
    margin-top: 4%;
}

form {
    text-align: center;
    margin-top: 2%;
}

.list-wrapper {
    width: 90vw;
}

.flex-wrap {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: center;
        justify-content: center;
    margin-top: 4%;
    width: 100%;
}
.notes {
    width: 300px;
    height: 250px;
    background-color: #bcc0e5;
    margin:0;
    margin: 6px;
    position: relative;
    border-radius: 3px;
    vertical-align: middle;
    padding: 30px;
    word-wrap: break-word;
    border: 1px solid #a0a5d8;
    text-align: center;
}

.controls {
    position: absolute;
    bottom: 0;
    text-transform: uppercase;
    width: 80%;
    margin-bottom: 13px;
    font-size: 10px;
}

input {
    outline: none;
    border: 1px solid black;
    width: 20vw;
    padding: 14px 22px;
    border-radius: 2px;
    border: 1px solid #c5c5c5;
}

button {
    color: white;
    background: #3e3e3e;
    border: none;
    margin-left: 2%;
    font-size: 14px;
    font-family: 'Open Sans', sans-serif;
    padding: 16px 25px;
    border-radius: 4px;
    cursor: pointer;
}

.edit , .delete {
    font-weight: 300;
    font-family: 'Lato', sans-serif;
    display: block;
    letter-spacing: 8px;
    color :#555656;

}
.edit {
    float: left;
}

.delete {
    float: right
}

.delete:hover {
    color: #e5e5e5;
}
.edit:hover {
    color: #e5e5e5;
}
.itemP {
    word-wrap: break-word;
    font-family: 'Open Sans', sans-serif;
    color: #4c4b4b;
}

.footer {
    text-align: center;
    font-family: 'Open Sans', sans-serif;
    padding-top: 110px;
    bottom: 0;
    right: 0;
    left: 0;
    margin: auto;
    margin-bottom: 5%;
}

@media screen and (max-width: 480px) {
    .display-4 {
        font-size: 28px;
        margin-bottom: 8%;
    }
    input {
        width: 60%;
        padding: 9px 22px;
    }

    button {
        padding: 12px 18px;
        font-size: 12px;
    }
    .notes {
        width: 44%;
        max-height: 150px;
        padding: 15px;
    }
    .itemP {
        font-size: 14px;
        line-height: 16px;
    }
    .controls {
        margin-bottom: 8px;
        font-size: 8px;
        width: 90%;
    }
}



